import React, { useState, useEffect } from 'react'
import API from '../../../utils/Api'
import Swal from 'sweetalert2'
import { useAuth } from "../../../context/auth"
import { useSubaccounts } from "../../../context/subaccount" 

import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row,
    Col
} from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWindowClose, faSave, faKey, faTimesCircle } from '@fortawesome/free-solid-svg-icons'

import { TextField, InputAdornment, IconButton, Paper } from '@material-ui/core'
import Visibility from '@material-ui/icons/Visibility'
import VisibilityOff from '@material-ui/icons/VisibilityOff'
import LinearProgressBarWithLabel from '../Common/LinearProgressBarWithLabel'

const ChangePasswordModal = (props) => {
    const authToken = useAuth().authTokens
    const subaccount_context = useSubaccounts()
    const [isProcessing, setIsProcessing] = useState(false)
    const [isShowPass, setIsShowPass] = useState(false)
    const toggleShowPassword = () => setIsShowPass(!isShowPass)

    const [isValidateError, setIsValidateError] = useState(false)
    const [isValidateMessage, setIsValidateMessage] = useState("Invalid Password")
    const [isMinimum, setIsMinimum] = useState(false)

    const [userID, setUserID] = useState('')
    const [password, setPassword] = useState('')
    

    const resetPasswordHandler = (e) => {
        e.preventDefault()
        const token = authToken.access_token

        if(isMinimum){
            setIsValidateMessage("Minimum password length is 6")
        }

        if (!isMinimum) {
            setIsValidateError(false)
            setIsProcessing(true)          

            let newPassData = {
               id: userID,
               new_password: password
            }


            API.post('store/update-password', newPassData, {
                headers: {
                    'Authorization': 'bearer ' + token,
                }
            }).then(result => {
                setIsProcessing(false)
                if (result.data.status) {
                    Swal.fire(
                        'Transaction Completed!',
                        'Password Successfully Updated!',
                        'success'
                    )

                    props.toggle()
                } else {
                    setIsValidateMessage(result.data.message)
                    Swal.fire(
                        'Transaction Failed!',
                        result.data.message,
                        'error'
                    )                    
                }
            }).catch(e => {
                setIsProcessing(false)
                Swal.fire(
                    'Transaction Failed!',
                    'Failed to Update Password!',
                    'error'
                )
            })
        }else{
            setIsValidateError(true)
        }
    }

    const handleChangePassword = (value) => {           
        setPassword(value)
        if (value.replace(/\s+/g, '').length >= 6) {
            setIsMinimum(false)  
        } else {  
            setIsMinimum(true)
        }   
    }

    useEffect(() => {
        setUserID(subaccount_context.selectedSubAccounts[0].id)        
        return () => {}
    }, [])

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-md" style={{ marginLeft: "40%", marginTop: "7%", color: "#1a1751" }}>
                <ModalHeader toggle={props.toggle}>
                    <FontAwesomeIcon icon={faKey} /> Reset Password <br />
                </ModalHeader>
                <form onSubmit={(e) => resetPasswordHandler(e)}>                    
                    <ModalBody>
                        {isValidateError &&
                            <div className="text-left card p-1 bg-danger">
                                <span className="text-white ml-2"><FontAwesomeIcon icon={faTimesCircle} /> { isValidateMessage }</span>
                            </div>
                        }
                        <Row className="p-2 pt-0">
                            <div className="col-md-12">
                                <Row>
                                    <div className="col-md-12 p-1">
                                        <Card className="p-1">
                                            <CardBody className="p-1 m-1">
                                                <div className="mt-1 mb-1">
                                                    <TextField
                                                        name="password"
                                                        label="Password"
                                                        type={isShowPass ? "text" : "password"}
                                                        onChange={(e) => handleChangePassword(e.target.value)}
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowPassword}>
                                                                    {isShowPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>                                                                                             
                                            </CardBody>
                                        </Card>
                                    </div>
                                </Row>
                            </div>
                            {isProcessing &&
                                <Col md={12}>
                                    <Paper className="p-2 m-2 mt-3">
                                        <LinearProgressBarWithLabel title="Resetting Password" />
                                    </Paper>
                                </Col>
                            }
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing || isValidateError} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                                </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                        </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    )


}

export default ChangePasswordModal 