import React, { useEffect, useState } from "react"
import API from "../../../utils/Api"
import Swal from "sweetalert2"
import { useAuth } from "../../../context/auth"
import { useProfile } from "../../../context/profile"
import { TextField } from "@material-ui/core"
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {
  faWindowClose,
  faSave,
  faUserEdit,
} from "@fortawesome/free-solid-svg-icons"

import { Paper, FormControlLabel, Switch } from "@material-ui/core"
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel"

const ChangeProfile = (props) => {
  const authToken = useAuth().authTokens
  const details = authToken.details
  const profile_context = useProfile()
  const [isProcessing, setIsProcessing] = useState(false)
  const [isEdit, setIsEdit] = useState(false)
  const [isDisabled, setIsDisabled] = useState(true)
  const [carrierID, setCarrierID] = useState("")
  const [carrierName, setCarriername] = useState("")
  const [carrierContact, setCarrierContact] = useState("")
  const [carrierAddress, setCarrierAddress] = useState("")
  const [carrierEmail, setCarrierEmail] = useState("")

  const handleEdit = () => {
    setIsEdit(!isEdit)
    if (isDisabled) {
      setIsDisabled(null)
    } else {
      setIsDisabled(true)
    }
  }

  const updateProfileHandler = (e) => {
    e.preventDefault()
    setIsProcessing(true)

    let postData = {
      id: carrierID,
      carrier: carrierName,
      contact: carrierContact,
      address: carrierAddress,
      email: carrierEmail,
    }

    API.post(`auth/carrier/update`, postData, {
      headers: {
        Authorization: "bearer " + authToken.access_token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false)

          Swal.fire(
            "Transaction Completed!",
            "Profile Updated!",
            "success"
          )

          profile_context.setUsername(carrierName)
          profile_context.getProfileDetails()
          props.toggle()
        } else {
          setIsProcessing(false)
          Swal.fire("Transaction Failed!", result.data.message, "error")
        }
      })
      .catch((e) => {
        setIsProcessing(false)
        Swal.fire("Transaction Failed!", "error")
      })
  }

  useEffect(() => {
    let carrier = profile_context.profileDetails
    setCarrierID(carrier.id)
    setCarriername(carrier.carrier_name)
    setCarrierContact(carrier.carrier_contact)
    setCarrierEmail(carrier.carrier_email)
    setCarrierAddress(carrier.carrier_address)
    return () => { }
  }, [])

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-md"
        style={{ marginLeft: "40%", marginTop: "7%", color: "#1a1751" }}
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faUserEdit} /> Update Profile <br />
        </ModalHeader>
        <form onSubmit={(e) => updateProfileHandler(e)}>
          <ModalBody>
            <FormControlLabel
              className="ml-2"
              control={
                <Switch
                  checked={isEdit}
                  onChange={handleEdit}
                  color="primary"
                />
              }
              label="Edit"
            />
            <div className="col-md-12">
              <Row>
                <div className="col-md-12 p-1">
                  <Card className="p-1">
                    <CardBody className="p-1 m-1">
                      {details.type === "main" && (
                        <div className="mt-1 mb-1">
                          <TextField
                            name="carrier"
                            label="Name"
                            value={carrierName || ""}
                            onChange={(e) => setCarriername(e.target.value)}
                            fullWidth
                            required
                            disabled={isDisabled}
                          />
                        </div>
                      )}
                      <div>
                        <TextField
                          name="contact"
                          label="Contact"
                          value={carrierContact || ""}
                          onChange={(e) => setCarrierContact(e.target.value)}
                          fullWidth
                          required
                          disabled={isDisabled}
                        />
                      </div>
                      <div className="mt-1 mb-1">
                        <TextField
                          name="email"
                          label="Email"
                          value={carrierEmail || ""}
                          onChange={(e) => setCarrierEmail(e.target.value)}
                          fullWidth
                          required
                          disabled={isDisabled}
                        />
                      </div>
                      <div className="mt-1 mb-1">
                        <TextField
                          name="address"
                          label="Address"
                          value={carrierAddress || ""}
                          onChange={(e) => setCarrierAddress(e.target.value)}
                          fullWidth
                          required
                          disabled={isDisabled}
                        />
                      </div>
                    </CardBody>
                  </Card>
                </div>
              </Row>
            </div>
            <Row className="p-2 pt-0">
              {isProcessing && (
                <Col md={12}>
                  <Paper className="p-2 m-2 mt-3">
                    <LinearProgressBarWithLabel title="Updating User Profile" />
                  </Paper>
                </Col>
              )}
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={!isEdit || isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Close
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  )
}

export default ChangeProfile
