import React, { useState, useEffect, useCallback } from "react"
import Axios from "axios"
import Swal from "sweetalert2"
import { useAuth } from "../../../context/auth"
import { SubaccountsContext } from "../../../context/subaccount"
import API from "../../../utils/Api"
import { debounce } from "lodash"
import { Tooltip, FormControlLabel, Switch } from "@material-ui/core"
import { Container, Button, Row, Col } from "reactstrap"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faEdit, faRecycle, faRedo, faRedoAlt } from "@fortawesome/free-solid-svg-icons"
import DataTable from "../../UI/Datatable/Datatable"
import MenuButton from "../../UI/Common/OptionsMenuSubAccountsButton"

import "./subaccounts.css"
import NewSubAccounts from "../../UI/Modals/NewSubAccounts"
import UpdateSubAccount from "../../UI/Modals/UpdateSubaccount"
import ResetPassword from "../../UI/Modals/ResetPassword"

const Ships = (props) => {
    const token = useAuth().authTokens
    const [subAccountsData, setSubAccountsData] = useState([])
    const [selectedSubAccounts, setSelectedSubAccounts] = useState("")
    const [currentPage, setCurrentPage] = useState()
    const [isLoading, setIsLoading] = useState(false)


    // modal toggle
    const [newSubAccounts, setNewSubAccounts] = useState(false)
    const toggleNewSubAccounts = () => setNewSubAccounts(!newSubAccounts)
    const [updateSubAccounts, setUpdateSubAccounts] = useState(false)
    const toggleUpdateSubAccounts = () => setUpdateSubAccounts(!updateSubAccounts)
    const [resetPassword, setResetPassword] = useState(false)
    const toggleResetPassword = () => setResetPassword(!resetPassword)

    const statusButton = (index, subaccount, data) => {
        return (
            <FormControlLabel
                className="ml-2"
                control={
                    <Switch
                        checked={subaccount.isActive ? true : false}
                        onChange={(e) => handleUpdateStatus(index, subaccount, data)}
                        color={subaccount.isActive ? "primary" : "default"}
                    />
                }
                label={subaccount.isActive ? "Active" : "Inactive"}
            />
        )
    }

    const handleUpdateStatus = (index, subaccount, data) => {
        let raw = data
        let message = "Activate this account?"
        let icon = "info"
        let title = "Activating"

        if (subaccount.isActive) {
            message = "Deactivate this account?"
            icon = "warning"
            title = "Deactivating"
        }

        Swal.fire({
            title: `${title} Account`,
            text: message,
            icon: icon,
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Yes",
        }).then((result) => {
            if (result.isConfirmed) {
                API.post(
                    `carrier/toggle`,
                    { id: subaccount.id, isActive: !subaccount.isActive },
                    {
                        headers: {
                            Authorization: "bearer " + token.access_token,
                        },
                    }
                ).then((result) => {
                    if (result.data.status) {
                        raw[index].isActive = !subaccount.isActive
                        let formattedData = formatData(raw)
                        setSubAccountsData(formattedData)
                    } else {
                        Swal.fire("Transaction Completed", result.data.message, "error")
                    }
                })
            }
        })
    }

    const handleUpdateProfile = (value) => {
        toggleUpdateSubAccounts()
        setSelectedSubAccounts(value)
    }

    const handleResetPassword = (value) => {
        toggleResetPassword()
        setSelectedSubAccounts(value)
    }

    const formatData = (data = null) => {
        if (data === null) {
            return null
        }

        return formatObjectData(data)
    }

    const formatObjectData = (data) => {
        let subAccounts = []
        let index = 0

        for (const [key, value] of Object.entries(data)) {
            subAccounts[index] = [
                value.id,
                value.username,
                value.carrier_email,
                value.carrier_contact,
                statusButton(index, value, data),
                [value, index, data],
            ]

            index++
        }

        return subAccounts
    }


    const loadSubAccounts = useCallback(() => {
        setIsLoading(true)
        let cancelToken = Axios.cancelToken
        API.get("carrier/sub-accounts", {
            cancelToken: cancelToken,
            headers: {
                Authorization: "bearer " + token.access_token,
            },
        })
            .then((result) => {
                let data = result.data.data
                let currentPage = result.data.current_page
                if (data.length > 0) {
                    let subaccounts = formatData(data)
                    setCurrentPage(currentPage)
                    setSubAccountsData(subaccounts)
                }

                setIsLoading(false)
            })
            .catch((e) => {
                setIsLoading(false)
            })
    }, [])

    const appendSubAccounts = (page) => {
        API.get(`carrier/sub-accounts?page=${page}`, {
            headers: {
                Authorization: "bearer " + token.access_token,
            },
        }).then((result) => {
            let data = result.data.data

            let newSubAccountsData = []
            let subaccounts = formatData(data)

            if (result.data.current_page > currentPage) {
                setCurrentPage(result.data.current_page)
                if (subaccounts !== undefined) {
                    newSubAccountsData = [...subAccountsData, ...subaccounts]
                } else {
                    newSubAccountsData = subAccountsData
                }
            }

            setSubAccountsData(newSubAccountsData)
        })
    }

    const debounceSearchHandler = debounce(
        (queryData) => searchSubAccounts(queryData),
        500
    )

    const searchSubAccounts = (query) => {
        return API.post(
            `carrier/sub-accounts/search`,
            {
                account: query,
            },
            {
                headers: {
                    Authorization: "bearer " + token.access_token,
                },
            }
        ).then((result) => {
            let data = result.data
            let subaccounts = formatData(data)
            setSubAccountsData(subaccounts)
            setIsLoading(false)
        })
    }

    const columns = [
        "ID",
        "Username",
        "Email",
        "Contact",
        "Status",
        {
            name: "Options",
            options: {
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div style={{ color: "#1a1751" }}>
                            <Tooltip title="Update Account" placement="top">
                                <Button
                                    color="link"
                                    className="p-1 m-0 btn"
                                    onClick={() => handleUpdateProfile(value)}
                                >
                                    <FontAwesomeIcon icon={faEdit} />
                                </Button>
                            </Tooltip>
                            <Tooltip title="Reset Password" placement="top">
                                <Button
                                    color="link"
                                    className="p-1 m-0 btn"
                                    onClick={() => handleResetPassword(value)}
                                >
                                    <FontAwesomeIcon className="text-danger" icon={faRedoAlt} />
                                </Button>
                            </Tooltip>
                        </div>
                    )
                },
            },
        },
    ]

    const options = {
        fixHeader: true,
        filterType: "dropdown",
        responsive: "vertical",
        tableBodyHeight: "350px",
        selectableRows: "none",
        filter: false,
        viewColumns: false,
        elevation: 0,
        print: false,
        download: false,
        rowsPerPage: 5,
        rowsPerPageOptions: [5, 30, 100],
        onChangePage: (p) => {
            if (p % 2 !== 0) {
                if (p >= currentPage) {
                    appendSubAccounts(currentPage + 1)
                }
            }
        },
        onSearchClose: () => {
            setIsLoading(false)
            loadSubAccounts()
        },
        searchProps: {
            onKeyUp: (e) => {
                let queryString = e.target.value
                if (queryString.length >= 2) {
                    setIsLoading(true)
                    debounceSearchHandler(queryString)
                }
            },
        },
        // this is to disable auto search on local data
        customSearch: (query, row, col) => {
            return subAccountsData.length > 0
        },
    }

    useEffect(() => {
        loadSubAccounts()
        return () => { }
    }, [loadSubAccounts])

    return (
        <SubaccountsContext.Provider value={{ subAccountsData, selectedSubAccounts, loadSubAccounts }}>
            <Container fluid="true" className="p-0 m-0 mt-5">
                <Row className="m-0 p-0 text-right">
                    <Col md={12}>
                        <Row>
                            <Col md={12} className="m-0 p-0">
                                <MenuButton toggle={toggleNewSubAccounts} />
                            </Col>
                        </Row>
                    </Col>
                    <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
                        <DataTable
                            title="Sub Accounts"
                            data={subAccountsData}
                            isLoading={isLoading}
                            columns={columns}
                            options={options}
                        />
                    </Col>
                </Row>
            </Container>
            {newSubAccounts && (
                <NewSubAccounts
                    isOpen={newSubAccounts}
                    toggle={toggleNewSubAccounts}
                    loadSubAccounts={loadSubAccounts}
                />
            )}

            {updateSubAccounts && (
                <UpdateSubAccount
                    isOpen={updateSubAccounts}
                    toggle={toggleUpdateSubAccounts}
                    loadSubAccounts={loadSubAccounts}
                />
            )}

            {resetPassword && (
                <ResetPassword
                    isOpen={resetPassword}
                    toggle={toggleResetPassword}
                />
            )}

        </SubaccountsContext.Provider>
    )
}

export default Ships
