import React, { useState } from "react" 
import API from "../../../utils/Api" 
import DateFnsUtils from "@date-io/date-fns" 
import { useAuth } from "../../../context/auth" 
import { useSchedule } from "../../../context/schedules" 
import { useTheme } from "../../../context/theme" 
import Swal from "sweetalert2" 
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap" 

import { TextField, FormControlLabel, Switch, Paper } from "@material-ui/core" 
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel" 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome" 
import {
  faCalendarDay,
  faInfoCircle,
  faSave,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons" 
import Error from "../Error/Error" 
import ShipNameSelect from "../../UI/Forms/Fields/ShipNameSelect" 
import ArrivalDate from "../../UI/Forms/Fields/ArrivalDate" 
import DepartureDate from "../../UI/Forms/Fields/DepartureDate" 
import { Typography, Checkbox } from "@material-ui/core" 

const UpdateSchedule = (props) => {
  const dateFns = new DateFnsUtils() 
  const authToken = useAuth().authTokens.access_token 
  const schedule_context = useSchedule() 
  const selectedShip = schedule_context.selectedSchedule[1] 
  const modalMarginLG = useTheme().modalMarginLG 

  const [isProcessing, setIsProcessing] = useState(false) 
  const [isError, setIsError] = useState(false) 
  const [isEdit, setIsEdit] = useState(false) 
  const [isDisabled, setIsDisabled] = useState(true) 
  const [errorMessage, setErrorMessage] = useState("Invalid Input") 
  const [isSelectedDeparture, setIsSelectedDeparture] = useState(false) 

  const [notify, setNotify] = useState(false) 
  const [currentShip, setCurrentShip] = useState(selectedShip.ship.id) 
  const [ship, setShip] = useState(selectedShip.ship.id) 
  const [voyage, setVoyage] = useState(selectedShip.voyage) 
  const [departure, setDeparture] = useState(
    dateFns.format(new Date(selectedShip.departure), "MMM/d/yyyy h:mm:ss a")
  ) 
  const [currentDeparture, setCurrentDeparture] = useState(
    dateFns.format(new Date(selectedShip.departure), "MMM/d/yyyy h:mm:ss a")
  ) 
  const [arrival, setArrival] = useState(
    dateFns.format(new Date(selectedShip.arrival), "MMM/d/yyyy h:mm:ss a")
  ) 
  const [fromLocation, setFromLocation] = useState(selectedShip.fromLocation) 
  const [toLocation, setToLocation] = useState(selectedShip.toLocation) 
  const [remarks, setRemarks] = useState("") 
  const [isChangeDeparture, setIsChangeDeparture] = useState(false) 

  const validate = () => {
    let d1 = dateFns.date(departure) 
    let d2 = dateFns.date(arrival) 

    if (isChangeDeparture) {
      if (dateFns.isAfter(d1, d2) || dateFns.isEqual(d1, d2)) {
        setIsError(true) 
        setErrorMessage("Invalid Departure Date") 
        return false 
      }
    }

    if (dateFns.isBefore(d2, d1) || dateFns.isEqual(d1, d2)) {
      setIsError(true) 
      setErrorMessage("Invalid Arrival Date") 
      return false 
    }

    return true 
  } 

  const saveNewRecordHandler = (e) => {
    e.preventDefault() 

    if (!validate()) {
      return null 
    }

    setIsProcessing(true) 
    let token = authToken 
    let postData = {
      transportation_id: ship,
      voyage: voyage,
      departure: dateFns.format(new Date(departure), "Y-M-dd H:m:s"),
      arrival: dateFns.format(new Date(arrival), "Y-M-dd H:m:s"),
      fromLocation: fromLocation,
      toLocation: toLocation,
      remarks: remarks,
      notify: notify,
    } 

    API.put(`carrier/transport/voyage/update/${selectedShip.id}`, postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false) 

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          ) 

          setNotify(false) 
          setCurrentDeparture("") 
          setCurrentShip("") 
          schedule_context.loadSchedules() 
          props.toggle() 
        } else {
          setIsProcessing(false) 
          Swal.fire("Transaction Failed!", result.data.message, "error") 
        }
      })
      .catch((e) => {
        setIsProcessing(false) 
        Swal.fire("Transaction Failed!", "error") 
      }) 
  } 

  const handleEdit = () => {
    setIsEdit(!isEdit) 
    if (isDisabled) {
      setIsDisabled(null) 
    } else {
      setIsDisabled(true) 
    }
  } 

  const handleDepartureDateChange = (selectedDate) => {
    let d1 = dateFns.date(currentDeparture) 
    let d2 = dateFns.date(selectedDate) 

    if (!dateFns.isEqual(d1, d2)) {
      setIsChangeDeparture(true) 
      setNotify(true) 
    }

    setIsSelectedDeparture(true) 
  } 

  const handleChangeShip = (shipId) => {
    if (shipId !== currentShip) {
      setNotify(true) 
    }
  } 

  const handleNotify = () => {
    setNotify(!notify)
  }

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        className="modal-lg"
        style={{ marginTop: "5%", marginLeft: modalMarginLG }}
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faCalendarDay} /> Update Schedule
        </ModalHeader>
        <form onSubmit={(e) => saveNewRecordHandler(e)}>
          <ModalBody className="pt-0 pb-0">
            {isError && (
              <Col md={12} className="text-left m-0 p-0 mt-2 ml-2">
                <Typography variant="h6">
                  <Error className="p-0 m-0">{errorMessage}</Error>
                </Typography>
              </Col>
            )}
            <Row className="p-2">
              <FormControlLabel
                className="ml-2"
                control={
                  <Switch
                    checked={isEdit}
                    onChange={handleEdit}
                    color="primary"
                  />
                }
                label="Edit"
              />
              <Col md={12}>
                <Card>
                  <CardBody className="pb-0">
                    <Row>
                      <Col md={12} className="mt-0 pt-0 mb-2 text-danger">
                        <h6>
                          <FontAwesomeIcon icon={faInfoCircle} /> Note: Please
                          fill in the required (*) fields
                        </h6>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={6}>
                        <ShipNameSelect
                          disabled={isDisabled}
                          shipName={ship}
                          setShipName={setShip}
                          handleChangeShip={(shipId) =>
                            handleChangeShip(shipId)
                          }
                        />
                        <TextField
                          className="m-0"
                          value={fromLocation || ""}
                          onChange={(e) => setFromLocation(e.target.value)}
                          name="fromLocation"
                          label="Origin"
                          required
                          fullWidth
                          disabled={isDisabled}
                        />
                        <TextField
                          className="m-0"
                          value={toLocation || ""}
                          onChange={(e) => setToLocation(e.target.value)}
                          name="toLocation"
                          label="Destination"
                          required
                          fullWidth
                          disabled={isDisabled}
                        />
                      </Col>
                      <Col md={6}>
                        <TextField
                          className="m-0"
                          value={voyage}
                          onChange={(e) => setVoyage(e.target.value)}
                          name="voyage"
                          label="Voyage Number"
                          required
                          fullWidth
                          disabled={isDisabled}
                        />
                        <DepartureDate
                          pickedDate={(selectedDate) =>
                            handleDepartureDateChange(selectedDate)
                          }
                          departureDate={departure}
                          setDepartureDateChange={setDeparture}
                          disabled={isDisabled}
                        />
                        <ArrivalDate
                          isSelectedDeparture={isSelectedDeparture}
                          departure={departure}
                          arrivalDate={arrival}
                          setArrivalDateChange={setArrival}
                          disabled={isDisabled}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12}>
                        <Paper className="mt-3 p-2">
                          <Typography>Remarks/Reason: </Typography>
                          <TextField
                            className="m-0"
                            value={remarks || ""}
                            onChange={(e) => setRemarks(e.target.value)}
                            name="remarks"
                            placeholder="Enter remarks or reasons for update..."
                            fullWidth
                            multiline
                            rows={1}
                            rowsMax={1}
                            required
                            disabled={isDisabled}
                            InputProps={{ disableUnderline: true }}
                          />
                        </Paper>
                      </Col>
                      <Col md={12}>
                        <FormControlLabel
                          className="mt-1"
                          control={
                            <Checkbox onClick={() => handleNotify()} checked={notify} color="primary" />
                          }
                          label="Notify Passengers"
                        />
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Updating Voyage Schedule" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing || !isEdit}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              type="button"
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  ) 
} 

export default UpdateSchedule 
